import * as React from "react";
import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface MediaObjectProp extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
  /**
   * Size of the nav
   */
  size?: "small" | "large";
}

export default class MediaObject extends NamespaceWrapper<MediaObjectProp> {
  render() {
    const {namespace, className, size, ...props} = this.props;
    const classes = this.classes(
      "MediaObject",
      size && "MediaObject--" + size,
      className
    );

    return <div className={classes} {...props} />;
  }
}

export interface MediaObjectMediaProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
  position ?: "left" | "right";
}

export class MediaObjectMedia extends NamespaceWrapper<MediaObjectMediaProps> {
  render() {
    const {namespace, className, position, ...props} = this.props;
    return <div {...props}
                className={this.classes("MediaObject__media", "MediaObject__media--" + (position ? position : "left"), className)} />;
  }
}

export interface MediaObjectImageProps extends React.ImgHTMLAttributes<HTMLImageElement>, NamespaceWrapperProps {
}

export class MediaObjectImage extends NamespaceWrapper<MediaObjectImageProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <img {...props}
                className={this.classes("MediaObject__image", className)} />;
  }
}

export interface MediaObjectContentProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
}

export class MediaObjectContent extends NamespaceWrapper<MediaObjectContentProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <div {...props}
                className={this.classes("MediaObject__content", className)} />;
  }
}
